.left__content{
    display: grid;
    height: auto;
    grid-gap: 38px 0px;
    padding: 0px 48px;

}

h1{
    font-size: 80px;
    font-weight: bold;
    line-height: 104px;
    text-align: left;
    vertical-align: top;
    margin-top: 0px;
}

p{
    font-size: 16px;
    font-style: regular;
    line-height: 24px;
    text-align: left;
    vertical-align: top;
}


.social{
    display: flex;
    align-items: center;
}

#root > div > div > div.left__content > section{
    display: grid;
    grid-gap: 601px 0px;
    margin-top: 176px;
}

#root > div > div > div.left__content > section > section:nth-child(1) > a:nth-child(n){
    color: #B2B2B2;
    display: block;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    word-spacing: 52px;
    width: fit-content;
    text-transform: uppercase;
    text-decoration: none;
}


#root > div > div > div.left__content > section > section.social > img{
    width: 48px;
    height: 48px;
    background: grey;
    border-radius: 25px;
}

#root > div > div > div.left__content > section  > section.social > a:nth-child(n){
    margin-left: 32px;
    color: #FFFFFF;
    text-transform: uppercase;
    text-decoration: none;
}



@media screen and (max-width: 834px){
    .left__content{
        display: grid;
        grid-template-columns: minmax(min-content, 576px);
        padding: 0px;
    }
    
    #root > div > div > div.left__content > section{
        display: none;
    }
}

@media screen and (max-width: 785px){
    #root > div > div > div.left__content > h1{
        font-size: 60px;
        line-height: 84px;

    }
}

@media screen and (max-width: 688px){
    #root > div > div > div.left__content > h1{
        font-family: IBM Plex Mono;
        font-style: normal;
        font-weight: bold;
        font-size: 48px;
        line-height: 62px;
    }

}